import createElement from './utils/createElement.mjs';
import getCSSVar from './utils/getCSSVar.mjs';
import ZHTMLElementInterface from './ZHTMLElementInterface.mjs';

export default class ZMenuBar extends ZHTMLElementInterface {
static __n(){return "ZMenuBar"};


    static TEMPLATE = createElement(`
        <style>
            :host{
                position:relative;
                display: flex;
                flex-direction: column;
                background-color: var(--color-menubar-background, rgb(235, 235, 235));
                width: 15rem; /* 240px */
                height: 100vh;
                overflow-x: hidden;
                overflow-y: hidden;
                flex-shrink: 0;
            }

            * {
                box-sizing: border-box;
            }

            .header {
                display: flex;
                justify-content: space-between;
                align-items: center;
                background-color: transparent;
                padding: var(--spacing-08, 2rem) var(--spacing-09, 2.5rem);
                z-index: 2;
            }

            button#mobile-menu-toggle-btn {
                /* reset default styles */
                border: none;
                background-color: transparent;
                font-family: inherit;
                padding: 0;
                overflow: visible;
                text-transform: none;
                cursor: pointer;

                /* add custom styles*/
                display: none;
            }

            ::slotted([slot="logo"]) {
                min-height: var(--font-size-08, 2rem);
                margin: auto 0;
            }

            ::slotted(a){
                text-decoration: none;
                color: inherit;
            }

            .body {
                flex-grow: 1;
                position: relative;
                padding: 0 1rem 1rem;
                background-color: transparent;
                overflow-y: auto;
            }

            hr {
                width: 50%;
                border: 0;
                background-color: var(--color-font);
                height: 1px;
            }

            .footer {
                position: absolute;
                bottom: 0;
                left: 0;
                width: inherit;
                max-height: 100%;
                z-index: 3;
                overflow-y: auto;
            }

            .menu-item-container {
                display: flex;
                flex-direction: column;
            }


            /* --breakpoint-m */
            @media (max-width: ${getCSSVar('--breakpoint-m') || '899px'}) {
                :host{
                    --header-height: calc(var(--font-size-08, 2rem) + var(--spacing-04, 0.75rem) * 2);
                    width: 100%;
                    background-color: transparent;
                    height: var(--header-height);
                    overflow-y: hidden;
                    transition-delay: var(--transition-duration-01, 0.2s);
                    transition-duration: 0s;
                    transition-property: height, overflow-y;
                }

                :host([open]) {
                    height: 100vh;
                    overflow-y: auto;
                    transition-delay: 0s;
                    transition-duration: 0s;
                    transition-property: height, overflow-y;
                }

                .header {
                    background-color: var(--color-virtually-white, rgb(235, 235, 235));
                    padding: var(--spacing-04, 0.75rem) var(--spacing-06, 1.25rem);
                }

                button#mobile-menu-toggle-btn {
                    display: inline-block;
                    min-width: var(--size-min-touch, 44px);
                    min-height: var(--size-min-touch, 44px);
                    margin: calc(var(--spacing-04, 0.75rem) * -1) 0; /* ignore padding of parent for correct positioning */
                }

                ::slotted([slot="logo"]) {
                    max-height: var(--font-size-08, 2rem);
                }

                .body {
                    visibility: hidden;
                    background-color: var(--color-menubar-body-background-mobile, #f8f8f8);
                    padding: var(--spacing-04, 12px) 0;
                    transform: translateX(100%);
                    opacity: 0;
                    max-height: calc(100vh - var(--header-height) - var(--size-height-min-button-profile-menu, 5rem));
                    //transition: transform 0.2s ease-out, opacity 0.2s ease-out, visibility 0.2s ease-out;

                    transition-duration: var(--transition-duration-01, 0.2s);
                    transition-delay: 0s;
                    transition-property: transform, opacity, visibility;
                    transition-timing-function: ease-out;
                }

                :host([open]) .body{
                    visibility: visible;
                    opacity: 1;
                    transform: translateX(0);
                }

                .footer {
                    visibility: hidden;
                    transform: translateX(100%);
                    opacity: 0;
                    max-height: calc(100vh - var(--header-height));
                    transition: transform 0.2s ease-out, opacity 0.2s ease-out, visibility 0.2s ease-out;
                }

                :host([open]) .footer{
                    visibility: visible;
                    opacity: 1;
                    transform: translateX(0);
                    bottom: 0;
                }

                .menu-open-icon {
                    display: inline-block;
                    pointer-events: none;
                }

                .menu-close-icon {
                    display: none;
                    pointer-events: none;
                }

                :host([open]) .menu-open-icon {
                    display: none;
                }

                :host([open]) .menu-close-icon {
                    display: inline-block;
                }
            }

        </style>
        <div class="header">
            <slot name="logo"></slot>
            <button id="mobile-menu-toggle-btn">
                <z-icon icon="essential-menu" class="menu-open-icon"></z-icon>
                <z-icon icon="essential-close" class="menu-close-icon"></z-icon>
            </button>
        </div>
        <nav class="body">
            <div class="body__core">
                <slot class="menu-item-container" name="menu-item-core"></slot>
            </div>
            <hr>
            <div class="body__apps">
                <slot class="menu-item-container" name="menu-item-app"></slot>
            </div>
        </nav>
        <div class="footer">
            <slot name="settings"></slot>
        </div>
    `);

    /**
     * Title text of the button to hide the mobile menu
     * @type {string}
     */
    hideMenuText = 'Hide Menu';

    /**
     * Title text of the button to show the mobile menu
     * @type {string}
     */
    showMenuText = 'Show Menu';

    /**
     *
     * @returns {string[]}
     */
    static get observedAttributes() {
        return ['open', 'titleshowmenu', 'titlehidemenu'];
    }

    /**
     *
     * @returns {boolean}
     */
    get open() {
        return this.hasAttribute('open');
    }

    /**
     *
     * @param {boolean} val
     */
    set open(val) {
        if (val === true) {
            this.setAttribute('open', '');
        }
        else {
            this.removeAttribute('open');
        }
    }

    /**
     *
     * @returns {string}
     */
    get titleHideMenu() {
        return this.getAttribute('titleHideMenu') || 'Hide Menu';
    }

    /**
     *
     * @param {string} val
     */
    set titleHideMenu(val) {
        this.setAttribute('titleHideMenu', val);
    }

    /**
     *
     * @returns {string}
     */
    get titleShowMenu() {
        return this.getAttribute('titleShowMenu') || 'Show Menu';
    }

    /**
     *
     * @param {string} val
     */
    set titleShowMenu(val) {
        this.setAttribute('titleShowMenu', val);
    }

    constructor() {
        super();
        this.toggleMobileMenu = this.toggleMobileMenu.bind(this);
    }

    /**
     *
     * @param {string} name
     * @param {string|null} oldValue
     * @param {string|null} newValue
     */
    attributeChangedCallback(name, oldValue, newValue) {
        if (newValue !== oldValue) {
            switch (name.toLowerCase()) {
                case 'titleshowmenu':
                    this.showMenuText = newValue;
                    this.updateButtonTitle();
                    break;
                case 'titlehidemenu':
                    this.hideMenuText = newValue;
                    this.updateButtonTitle();
                    break;
                case 'open':
                    if(newValue === null){
                        this.closeProfileMenu();
                    }
                    break;
            }
        }
    }

    /**
     * Lifecycle callback:
     * Invoked each time the custom element is appended into a document-connected element.
     */
    connectedCallback() {
        this.menuButton = this.shadowRoot.getElementById('mobile-menu-toggle-btn');

        // get profileMenu from last slot
        const slots = this.shadowRoot.querySelectorAll('slot');
        this.profileMenu = slots[slots.length-1]?.assignedElements()[0];

        ZMenuBar.observedAttributes.forEach((attr) => this.upgradeProperty(attr));
        this.updateButtonTitle();
        this.menuButton.addEventListener('click', this.toggleMobileMenu);
    }

    /**
     * Lifecycle callback:
     * Invoked each time the custom element is removed from a document-connected element.
     */
    disconnectedCallback() {
        this.menuButton.removeEventListener('click', this.toggleMobileMenu);
        this.menuButton = null;
    }

    /**
     * Toggles open attribute
     */
    toggleMobileMenu() {
        this.open = !this.open;
        this.updateButtonTitle(this.open);
    }

    updateButtonTitle() {
        if (this.menuButton !== undefined) {
            this.menuButton.title = this.open ? this.hideMenuText : this.showMenuText;
        }
    }

    closeProfileMenu() {
        if(this.profileMenu !== undefined){
            this.profileMenu.open = false;
        }
    }
}