import createElement from './utils/createElement.mjs';
import ZHTMLElementInterface from './ZHTMLElementInterface.mjs';

export default class ZLogo extends ZHTMLElementInterface {
static __n(){return "ZLogo"};


    static TEMPLATE = createElement(`
        <style>
            :host{
                --gap: var(--spacing-03, 0.5rem);
                display: inline-flex;
                flex-flow: row nowrap;
                align-items: center;
                color: var(--color-font, rgba(0,0,0,0.84));
                font-family: var(--font-family-primary, 'Roboto'), sans-serif;
                font-size: var(--font-size-logo, 1.25rem);
                max-width: 160px;
                max-height: var(--size-max-logo, 32px);
                overflow: hidden;
            }

            :host, ::slotted(*), *{
                box-sizing: border-box;
            }

            .logo-image{
                display: inline-block;
                height: var(--size-max-logo, 32px);
            }

            ::slotted([slot="image"]){
                height: 100%;
                width: auto;
            }

            ::slotted([slot="text"]){
                display: inline-flex;
                margin: auto 0;
            }

            .divider{
                display: inline-block;
                padding: 0 var(--gap);
                margin-right: -2px; /* visual correction */
            }

        </style>
        <span class="logo-image">
            <slot name="image"></slot>
        </span>
        <span class="divider">|</span>
        <slot class="text" name="text"></slot>
    `);

    connectedCallback() {
        this.hideDividerOnImageOnlyLogos();
    }

    hideDividerOnImageOnlyLogos(){
        const slots = this.shadowRoot.querySelectorAll('slot');
        const textSlot = slots[slots.length-1]?.assignedElements()[0];
        if(textSlot?.textContent === undefined){
            const divider = this.shadowRoot.querySelector('.divider');
            divider.style.display = 'none';
        }
    }
}